import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {
  currentTime: string;
  currentDate: string;
  apiUrl = environment.apiUrl;
  userid: string | null = localStorage.getItem('userid');
  selector='5c';
  summary:any;
  orders:any;
  daily:any;
  stats:any;
  groupedProducts: any[] = [];
  clockinview=true;
  summaryview=false;
  
  activePage: string = 'clockin'; // Set the default active page
  servicetotal=0.00;
  apiResponse:any;clockin:any;
  closedayview=false;
  constructor(
    private http: HttpClient,
    private router: Router,
    private cdr: ChangeDetectorRef // Inject ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.updateTime();
    this.checkauth();
   this.getreceipts();
  }
  checkauth() {
    const userid = localStorage.getItem('userid');
    const username = localStorage.getItem('username');
    const token = localStorage.getItem('token');

    if (userid && username && token) {
      const data = {
        token: token,
        userid: userid,
        username: username
      };

      const headers = new HttpHeaders({
        'Content-Type': 'application/json'
      });

      this.http.post(this.apiUrl + 'checkauth', data, { headers }).subscribe(
        (response: any) => {
          if (response.error === false) {
            if(response.clockin===false){
              this.router.navigate(['clockin']);
            }
            //this.router.navigate(['']);
          } else {
            this.router.navigate(['']);
          }
        },
        (error) => {
          console.error('Error during login:', error);
        }
      );
    } else {
      this.router.navigate(['']);
    }
  }
  getreceipts(){
    this.http.get(this.apiUrl + 'getuser').subscribe(
      (response: any) => {
        if (response.error === false) {
         this.orders=response.today;
         this.daily=response.daily;
          //this.router.navigate(['']);
        } else {
          this.router.navigate(['']);
        }
      },
      (error) => {
        console.error('Error during login:', error);
      }
    );
  }
  updateTime() {
    const updateClock = () => {
      const now = new Date();
      this.currentDate = now.toLocaleDateString();
      this.currentTime = now.toLocaleTimeString();
      this.cdr.detectChanges(); // Trigger change detection manually
    };
    // Call updateClock initially
    updateClock();
    // Set up a timer to update the time every second
    setInterval(updateClock, 1000);
  }

}
